.card {
  position: relative;
  height: 50vh;
  width: 100%;
  object-fit: cover;
  background-size: cover;
  background: rgba (0, 0, 0, 0.7);
}

.card:hover {
  background-color: rgba(0, 0, 0, 0.7);
  background-blend-mode: darken;
  transition: 0.6s;
}

.cardWrap {
  visibility: hidden;
  opacity: 0;
  color: white;
  max-width: 400px;
  width: 100%;
  text-align: center;
  position: relative;
  top: 50%;
  -ms-transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.card:hover .cardWrap {
  visibility: visible;
  opacity: 1;
  bottom: 30px;
  transition: 0.5s;
  color: white;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

.cardTitle {
  font-size: 16px;
  font-weight: 600;
}

.cardText {
  font-size: 14px;
}

.websiteBtn {
  text-decoration: none;
  border: 1px solid grey;
  border-radius: 5px;
  padding: 10px;
  color: white;
  margin: auto;
}

.websiteBtn:hover {
  color: white;
  background-color: rgba(255, 255, 255, 0.5);
  transition: 0.3s;
}

.reviewBtn {
  text-decoration: none;
  border: 1px solid grey;
  border-radius: 5px;
  padding: 10px;
  color: white;
  margin-left: 15px;
}

.reviewBtn:hover {
  color: white;
  background-color: rgba(255, 255, 255, 0.5);
  transition: 0.3s;
}

/* View review */

.reviewCard {
  padding: 20px;
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  border-radius: 5px;
}

.textarea {
  display: block;
  width: 600px;
  height: 300px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 10px;
  border: 1px solid #ccc;
}

.leaveReviewBtn {
  display: block;
  margin-left: auto;
  margin-right: auto;
  background-color: #e9c73f;
  color: white;
  border-radius: 5px;
  border: 1px solid #e9c73f;
  padding: 8px;
  width: 15%;
  margin-bottom: 50px;
}

.postReviewBtn {
  display: block;
  margin-left: auto;
  margin-right: auto;
  background-color: #e9c73f;
  color: #fff;
  border-radius: 5px;
  border: 1px solid #e9c73f;
  padding: 8px 30px;
  margin-top: 30px;
  margin-bottom: 70px;
}

.editReviewBtn {
  background-color: red;
  color: white;
  border-radius: 5px;
  border: 1px solid red;
  padding: 7px 30px;
  margin-top: 30px;
  margin-bottom: 70px;
}

.editCancelReviewBtn {
  background-color: red;
  color: white;
  border-radius: 5px;
  border: 1px solid red;
  padding: 7px 15px;
  margin-left: 10px;
}

.buttonWrap {
  text-align: center;
}

.editBtn {
  color: white;
  border-radius: 5px;
  border: 1px solid #84c5cd;
  background-color: #84c5cd;
  padding: 8px;
  margin-left: 15px;
  margin-right: 5px;
}

.deleteBtn {
  color: white;
  border-radius: 5px;
  border: 1px solid #c36262;
  background-color: #c36262;
  padding: 8px;
  margin-right: 5px;
}

.locateStars {
  display: inline-block;
}

.starabilityResult {
  position: relative;
  width: 150px;
  height: 30px;
  background-image: url("data:image/png;base64,");
  font-size: 0.1em;
  color: transparent;
}

.starabilityResult:after {
  content: " ";
  position: absolute;
  left: 0;
  height: 30px;
  background-image: url("data:image/png;base64,");
  background-position: 0 -30px;
}

.starabilityResult[data-rating="5"]::after {
  width: 150px;
}

.starabilityResult[data-rating="4.5"]::after {
  width: 135px;
}

.starabilityResult[data-rating="4"]::after {
  width: 120px;
}

.starabilityResult[data-rating="3.5"]::after {
  width: 105px;
}

.starabilityResult[data-rating="3"]::after {
  width: 90px;
}

.starabilityResult[data-rating="2.5"]::after {
  width: 75px;
}

.starabilityResult[data-rating="2"]::after {
  width: 60px;
}

.starabilityResult[data-rating="1.5"]::after {
  width: 45px;
}

.starabilityResult[data-rating="1"]::after {
  width: 30px;
}

.starabilityResult[data-rating="0.5"]::after {
  width: 15px;
}

@media screen and (-webkit-min-device-pixel-ratio: 2),
  screen and (min-resolution: 192dpi) {
  .starabilityResult {
    background-image: url("data:image/png;base64,");
    background-size: 30px auto;
  }
  .starabilityResult:after {
    background-image: url("data:image/png;base64,");
    background-size: 30px auto;
  }
}

.starability-basic {
  display: block;
  position: relative;
  width: 150px;
  min-height: 60px;
  padding: 0;
  border: none;
}

.starability-basic > input {
  position: absolute;
  margin-right: -100%;
  opacity: 0;
}

.starability-basic > input:checked ~ label,
.starability-basic > input:focus ~ label {
  background-position: 0 0;
}

.starability-basic > input:checked + label,
.starability-basic > input:focus + label {
  background-position: 0 -30px;
}

.starability-basic > input[disabled]:hover + label {
  cursor: default;
}

.starability-basic > input:not([disabled]):hover ~ label {
  background-position: 0 0;
}

.starability-basic > input:not([disabled]):hover + label {
  background-position: 0 -30px;
}

.starability-basic > input:not([disabled]):hover + label::before {
  opacity: 1;
}

.starability-basic > input:focus + label {
  outline: 1px dotted #999;
}

.starability-basic .starability-focus-ring {
  position: absolute;
  left: 0;
  width: 100%;
  height: 30px;
  outline: 2px dotted #999;
  pointer-events: none;
  opacity: 0;
}

.starability-basic > .input-no-rate:focus ~ .starability-focus-ring {
  opacity: 1;
}

.starability-basic > label {
  position: relative;
  display: inline-block;
  float: left;
  width: 30px;
  height: 30px;
  font-size: 0.1em;
  color: transparent;
  cursor: pointer;
  background-image: url("data:image/png;base64,");
  background-repeat: no-repeat;
  background-position: 0 -30px;
}

.starability-basic > label::before {
  content: "";
  position: absolute;
  display: block;
  height: 30px;
  background-image: url("data:image/png;base64,");
  background-position: 0 30px;
  pointer-events: none;
  opacity: 0;
}

.starability-basic > label:nth-of-type(5)::before {
  width: 120px;
  left: -120px;
}

.starability-basic > label:nth-of-type(4)::before {
  width: 90px;
  left: -90px;
}

.starability-basic > label:nth-of-type(3)::before {
  width: 60px;
  left: -60px;
}

.starability-basic > label:nth-of-type(2)::before {
  width: 30px;
  left: -30px;
}

.starability-basic > label:nth-of-type(1)::before {
  width: 0px;
  left: 0px;
}

@media screen and (-webkit-min-device-pixel-ratio: 2),
  screen and (min-resolution: 192dpi) {
  .starability-basic > label {
    background-image: url("data:image/png;base64,");
    background-size: 30px auto;
  }
}

@media screen and (-ms-high-contrast: active) {
  .starability-basic {
    width: auto;
  }
  .starability-basic > input {
    position: static;
    margin-right: 0;
    opacity: 1;
  }
  .starability-basic .input-no-rate {
    display: none;
  }
  .starability-basic > label {
    display: inline;
    float: none;
    width: auto;
    height: auto;
    font-size: 1em;
    color: inherit;
    background: none;
  }
  .starability-basic > label::before,
  .starability-basic > label::after {
    display: none;
  }
}

@media (max-width: 575.98px) {
  .reviewCard {
    width: 360px;
    margin-left: auto;
    margin-right: auto;
  }

  .textarea {
    width: 400px;
  }

  .postReviewBtn {
    padding: 10px 30px;
  }

  .editReviewBtn {
    padding: 7px 30px;
  }

  .editCancelReviewBtn {
    padding: 7px 15px;
    margin-left: 10px;
  }
}
